/* 
COLOR PALLETE
white = #f5f5f5
black1 =  #444444
black2 = #111111
*/
body,
html {
	height: 100%;
	margin: 0;
	color: #444444;
	font-family: Lato, sans-serif;
}
.white {
	color: #f5f5f5;
}
.bgdark {
	background-color: #111111;
}
.aboutme {
	font-size: 3vh;
	text-align: justify;
}
.menu {
	position: fixed;
	display: flex;
	flex-direction: column;
	align-items: center;
	right: 0;
	top: 50%;
	overflow: hidden;
	transform: translate(-10%, -50%);
	border-radius: 10px;
	background-color: rgba(0, 0, 0, 0.15);
}
.socialNetwork {
	margin-top: 10vh;
	display: flex;
	justify-content: space-between;
}
.socialNetwork a {
	fill: #444444;
}
.btn {
	display: inline-block;
	vertical-align: top;
	padding: 8px 14px 10px;
	margin-bottom: 3px;
	border: 4px solid;
	color: #444444;
	border-radius: 9px;
	font-size: 18px;
	font-weight: 300;
	text-decoration: none;
}
.btn:hover {
	cursor: default;
	border-bottom-width: 7px;
	margin-bottom: 0;
}
.icons {
	display: flex;
	flex: 1;
	flex-direction: column;
	justify-content: space-between;
}
.menu .btnmenu {
	display: flex;
	flex-direction: column;
	width: 7vh;
	height: 7vh;
	justify-content: center;
}
.menuhover:hover {
	background-color: rgba(0, 0, 0, 0.6);
}
.icon {
	fill: #f5f5f5;
	padding: 5px;
}
.icon:hover {
	fill: #4cf0ff;
}
.profile {
	display: flex;
	align-items: center;
	justify-content: center;
	min-height: 100vh;
	background-color: #f5f5f5;
	padding-left: 50px;
	padding-right: 50px;
	flex-flow: wrap;
}
.profile .desc {
	width: 60vh;
	flex-direction: column;
}
.profile .img {
	margin: 5vh;
	width: 50vh;
	height: 50vh;
	background-size: contain;
	background-repeat: no-repeat;
	background-position: center;
}
.profile h1 {
	margin: 0;
}
.profile i {
	font-family: monospace;
}
.portfolio {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	min-height: 100vh;
}
img {
	display: block;
}
.portfolio-list {
	max-width: 80vw;
}
.masonry {
	-webkit-columns: 3;
	columns: 3;
	-webkit-column-gap: 40px;
	column-gap: 40px;
}
@media (max-width: 990px) {
	.masonry {
		-webkit-columns: 2;
		columns: 2;
	}
}
@media (max-width: 560px) {
	.masonry {
		-webkit-columns: 1;
		columns: 1;
	}
}
.masonry .card {
	-webkit-column-break-inside: avoid;
	page-break-inside: avoid;
	break-inside: avoid;
	padding-bottom: 40px;
	width: 50% \9;
	float: left\9;
	padding-left: 10px\9;
	padding-right: 10px\9;
	color: #444444;
}
.masonry .card:nth-child(2n+1) {
	clear: both\9;
}
.masonry .masonry-item {
	display: inline-block;
	width: 100%;
	box-shadow: rgb(10, 10, 10,1) 0 6px 8px;
	background: #f5f5f5;
	border-radius: 10px;
	overflow: hidden;
}
.card {
	padding: 0;
	margin: 0;
}
.card .caption {
	padding: 15px;
}
.card .caption span {
	font-size: 12px;
}
.card .caption h3 {
	margin: 0;
}
.card img {
	width: 100%;
}
.skills {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	min-height: 100vh;
}
.title {
	margin-bottom: 5vw;
	display: flex;
	align-items: center;
	flex-direction: row;
	margin-top: 10vh;
}
.title h1,
h2 {
	margin: 20px;
}
.skills .list {
	flex-wrap: nowrap;
	width: 80%;
	display: flex;
	flex-flow: wrap;
	text-align: center;
	align-content: flex-start;
	margin-bottom: 5vw;
}
.skills .item {
	overflow: auto;
	margin: 10px;
	padding: 20px;
	background-color: #111111;
	flex-grow: 1;
	border-radius: 10px;
	box-shadow: rgb(17, 17, 17,0.4) 0 6px 8px;
	overflow-wrap: break-word;
}
.skills .item p {
	margin: 0;
	font-size: 18px;
	font-family: monospace;
	color: #969696;
}
.desc .myname {
	font-weight: 900;
	font-size: 5vh;
	width: 100%;
}
.myname p {
	margin: 0;
	padding: 0;
}
.desc .focus {
	width: 100%;
	font-weight: 900;
	font-size: 3vh;
	margin-bottom: 20px;
}
.desc .as {
	width: 100%;
	margin-bottom: 20px;
}
.desc p {
	margin: 0;
}
.footer {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 25vh;
	font-family: monospace;
	font-size: 1.8vh;
	color: #f5f5f5;
	background-color: #111111;
}
.footercontent {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	justify-content: center;
}
.footericon {
	margin: 10px;
}

html::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
}

html::-webkit-scrollbar
{
	width: 6px;
	background-color: #F5F5F5;
}

html::-webkit-scrollbar-thumb
{
	background-color: #000000;
}
